import React from "react";
import { useSelector } from "react-redux";

import SelectModelCommonPage from "pages/SelectModelCommon";

const SelectModelTwoPage = () => {
  const selected_model = useSelector(store => store.backend.backend_details?.modelType);
  return (
    <>
      <SelectModelCommonPage path="/backend-select-techstack" selected_model={selected_model} />
    </>
  );
};

export default SelectModelTwoPage;
